'use client';

import React, { useEffect, useState } from 'react';
import { useTheme } from 'next-themes';
import {
  Accordion,
  AccordionItem,
  AccordionTrigger,
  AccordionContent,
} from '@/components/ui/accordion';

type Message = {
  id: string;
  title: string;
  beneficiaryMessageText: string;
  status: string;
  createdAt: string;
  beneficiary?: {
    firstName: string;
    lastName: string;
  };
};

const BeneficiaryMessagesAccordion: React.FC<{ className?: string }> = ({ className = '' }) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [loading, setLoading] = useState(true);
  const { resolvedTheme } = useTheme();

  useEffect(() => {
    fetch('/api/beneficiaryMessages')
      .then((res) => res.json())
      .then((data) => {
        setMessages(Array.isArray(data) ? data : []);
        setLoading(false);
      })
      .catch(() => setLoading(false));
  }, []);

  const containerBg =
    resolvedTheme === 'dark' ? 'bg-gray-900 border-gray-700' : 'bg-white border-pink-200';
  const titleColor = resolvedTheme === 'dark' ? 'text-pink-300' : 'text-pink-700';

  return (
    <div className={`max-w-none w-full ${containerBg} rounded-2xl shadow p-6 ${className}`}>
      <h2 className={`text-2xl font-bold mb-6 text-left ${titleColor}`}>
        Recent Beneficiary Messages
      </h2>

      {loading && <div className="text-center text-sm text-gray-400">Loading...</div>}
      {!loading && messages.length === 0 && (
        <div className="text-center text-sm text-gray-400">No beneficiary messages yet.</div>
      )}

      {!loading && (
        <Accordion type="single" collapsible className="w-full space-y-2">
          {messages.slice(0, 5).map((msg) => (
            <AccordionItem
              key={msg.id}
              value={msg.id}
              className={`border rounded-lg ${
                resolvedTheme === 'dark'
                  ? 'border-gray-700 bg-gray-800'
                  : 'border-pink-100 bg-pink-50'
              }`}
            >
              <AccordionTrigger className="px-4 py-2 text-lg font-semibold">
                {msg.title}
              </AccordionTrigger>
              <AccordionContent className="px-4 pb-4 text-sm space-y-2">
                <div className="text-gray-500">
                  {msg.beneficiary
                    ? `By ${msg.beneficiary.firstName} ${msg.beneficiary.lastName}`
                    : 'By Beneficiary'}{' '}
                  • {new Date(msg.createdAt).toLocaleDateString()}
                </div>
                <div className="text-base">
                  {typeof msg.beneficiaryMessageText === 'string'
                    ? msg.beneficiaryMessageText
                    : JSON.stringify(msg.beneficiaryMessageText)}
                </div>
                <div className="text-xs text-pink-500 capitalize">{msg.status}</div>
              </AccordionContent>
            </AccordionItem>
          ))}
        </Accordion>
      )}
    </div>
  );
};

export default BeneficiaryMessagesAccordion;
